
import Swiper from 'vux/src/components/swiper/swiper.vue'

export default {
props: ['card', 'chunkedImages', 'images'],
data () {
return {
showPreview: false,
previewIndex: 0
}
},
components: {
Swiper
},
computed: {
imgList () {
return this.images.map(item => {
return {
img: item.src
}
})
}
},
methods: {
viewImage (row, index) {

this.showPreview = true
this.previewIndex = row * 3 + index
},
stopPreview () {
this.showPreview = false
}
}
}



